<template>
    <div>
        <header>
            <h1>
                Circle drawing
            </h1>
        </header>
        <p>
            This is a tutorial about circle drawing on a grid.
        </p>
        <figure id="diagram">
            <svg viewBox="0 0 550 220">
                <rect v-for="({ q, r }, index ) in positions" :key="index" :x="q * scale + 1 / 2" :y="r * scale + 1 / 2"
                    :width="scale - 1" :height="scale - 1" fill="hsl(250 10% 93%)" />
            </svg>
        </figure>
        <footer>
            Last updated 1 Jan 2023.
        </footer>
    </div>
</template>

<script>
import * as d3 from "d3";

export default {
    name: 'D3Learning',
    data() {
        return {
            radius: 50,
            scale: 22,
            gridCols: 25,
            gridRows: 10,
            positions: [],
        }
    },
    mounted() {
        // this.draw()
        for (let q = 0; q < this.gridCols; q++) {
            for (let r = 0; r < this.gridRows; r++) {
                this.positions.push({ q, r });
            }
        }
    },
    methods: {
        draw() {
            const svg = d3.select('div')
                .append('svg')
                .attr('width', 500)
                .attr('height', 500);

            svg.append('circle')
                .attr('cx', 250)
                .attr('cy', 250)
                .attr('r', this.radius)
                .attr('fill', 'red');

            this.$watch('radius', (newVal, oldVal) => {
                console.log(oldVal)
                svg.select('circle')
                    .transition()
                    .duration(1000)
                    .ease(d3.easeBounce)
                    .attr('r', newVal)
            });
        }
    }
}
</script>

<style lang="scss" scoped>
svg {
    display: block;
    background: white;
}
</style>